import { Sequelize, DataTypes } from 'sequelize'
import moment from 'moment'
import sequelize from './sequelize'

// 宣讲会
const XuanjianghuiModel = sequelize.define('XuanjianghuiModel', {
	id: {
		type: DataTypes.BIGINT,
		primaryKey: true,
		autoIncrement: true,
		allowNull: false,
		comment: '主键id'
	},
	biaoti: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '标题'
	},
	fengmian: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '封面'
	},
	zhaopinzhiwei: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '招聘职位'
	},
	zhaopinrenshu: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '招聘人数'
	},
	jubandidian: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '举办地点'
	},
	jubanshijian: {
		type: DataTypes.DATE,
		allowNull: true,
		get() {
            return moment(this.getDataValue('jubanshijian')).format('YYYY-MM-DD HH:mm:ss')
        },
		comment: '举办时间'
	},
	xuanjianghuixiangqing: {
		type: DataTypes.TEXT,
		defaultValue: '',
		allowNull: true,
		comment: '宣讲会详情'
	},
	qiyezhanghao: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '企业账号'
	},
	qiyemingcheng: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '企业名称'
	},
	lianxidianhua: {
		type: DataTypes.STRING,
		defaultValue: '',
		allowNull: true,
		comment: '联系电话'
	},
	addtime: {
  		type: DataTypes.DATE,
  		defaultValue: DataTypes.NOW,
    	allowNull: false,
    	get() {
            return moment(this.getDataValue('addtime')).format('YYYY-MM-DD HH:mm:ss')
        },
		comment: '添加时间'
	}
}, {
	timestamps: false,
	freezeTableName: true,
	tableName: 'xuanjianghui'
})

export default XuanjianghuiModel
